*{outline:none !important}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

body{margin:0;font-family:"Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

@font-face {
    font-family: "IconsCourses";
    src: url(/web-courses/static/media/IconsCourses.e2390b7d.ttf) format("truetype");
}

i[class^="icon-"]:before, i[class*=" icon-"]:before {
    font-family: IconsCourses !important;
    font-style: normal;
    font-weight: normal !important;
    font-feature-settings: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon-class:before {
    content: "\f101";
}
.icon-study:before {
    content: "\f102";
}

*{box-sizing:border-box}html{font-size:62.5%}.global-flex,.global-center,.global-align-center,.global-justify-center,.global-justify-between,.global-direction-column{display:flex}.global-center{justify-content:center;align-items:center}.global-align-center{align-items:center}.global-justify-center{justify-content:center}.global-justify-between{justify-content:space-between}.global-direction-column{flex-direction:column}

